      <div class='nav' style='width:60%'>
        
        <div class="search">
          
          <van-search 
            placeholder="搜索" 
            @focus = 'action = true' 
            @blur='action=false'
            v-model="seo" 
            shape='round' 
            @cancel = 'seo = ""'
          />

          <div class="show" :class="seoShow?'todown':''">
            <div>
              <el-tree
                v-show='seo'
                accordion 
                :data="config.data" 
                :props="config.defaultProps" 
                @node-click="handleNodeClick2"
                node-key="id" 
                :filter-node-method="filterNode" 
                :default-expand-all='true'
                :expand-on-click-node='false'
                :highlight-current='true'
                ref="three" 
              />
            </div>
            <p><img class="back" src='./assets/11.png' @click='seoShow = false' /></p>
          </div>

        </div>

        <div class="navList">
          <span style='display:block;text-align:left;margin-left:12px;color:#5481fa;padding:1em 0 .9em 0;'
            @click = '$router.push({ path: "/home" });acitve =""'
          >简介</span>
          
          <el-tree 
            accordion 
            :data="config.data" 
            :props="config.defaultProps" 
            @node-click="handleNodeClick"
            node-key="id" 
            :filter-node-method="filterNode" 
            :default-expand-all='true'
            :expand-on-click-node='false'
            :highlight-current='true'
          />
          
        </div>
        
      </div>

      <div class='con' :class='acitve'  @click.stop="acitve =''"> 

        <div class='list' @click.stop='acitve=acitve?"":"show"' v-if="path2">
          {{!acitve?'目 录':'收 起'}}
          <img v-if='acitve' src='./assets/8.png' />
          <img v-else src='./assets/10.png' />
        </div>

        <div class='tem' style='height:100%;overflow-y: auto;padding: 25px 15px 10px;box-sizing:border-box'>

          <router-view style='min-height: calc(100% - 110px);padding-top:1px;'></router-view>

