</template>

<script>
  let config = require( './config' ); // 导航栏对应表
  
  require('./assets/re.css');

  export default {
    name: 'app',

    data(){
      return {
        seo: '', // 搜索关键字
        config, // 导航栏结构
        
        acitve: '', // 是否展开导航栏
        time: '',

        seoShow: false,
      }
    },

    methods: {
      
      handleNodeClick2( { id } ){
        this.handleNodeClick( { id } );
        this.seoShow = false;
        this.seo = '';
      },
      handleNodeClick( { id } ){
        if( '/'+id !==this.$route.path ){
          this.acitve ='';
          this.$router.push({ path: '/'+id })
        }
      },
      filterNode(val ,data){
        if( !val ) return true;
        return data.label.indexOf( val ) !== -1;
      },
     },

computed: {
    path2:function(){
      let path2=this.path;
      if ("/home" === this.$route.path){
      return path2;
    }else{
                return !path2;
             }
  },
   
  },
  
    watch: {
      seo(val){
        this.$refs.three.filter(val);
        if( val != '' ){
          this.seoShow = true;
        }
      }

      
    }
    
  }
</script>

