import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

const routes = [{
  path: '/',
  redirect: '/home'
}];

require.context( '../views/' ,true ,/\.vue/ ).keys().forEach( filename => {
  
  let name = filename.replace( /(.\/|.vue)/g ,'' );

  routes.push({
    path: '/'+ name,
    name,
    component: () => import( `../views/${ name }.vue` )
  })

})

const router = new VueRouter({
  routes
})

export default router
