import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import {Tree} from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(Tree);

Vue.config.productionTip = false

import { Search , Icon } from 'vant';
Vue.use(Search);
Vue.use(Icon);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
